html,
body {
  font-family: "Patua One", cursive;
}

body {
  background-size: 100% 100%;
  object-fit: cover;
}

button {
  cursor: pointer;
  padding: 0.5em;
  font-size: 1.2em;
}

.ghost {
  position: relative;
  top: 0;
  left: 0;
  width: 30vh;
  animation-duration: 1.5s;
  animation-name: hover;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

.ghost__body {
  height: 100%;
}

.ghost__hat {
  position: absolute;
  z-index: 2;
}

.ghost__hat--1 {
  top: -8%;
  left: 25%;
  width: 50%;
}

.ghost__hat--2 {
  top: -2%;
  left: 33%;
  width: 30%;
}

.ghost__hat--3 {
  top: 0;
  left: 29%;
  width: 40%;
}

.ghost__hat--4 {
  top: -22%;
  left: 35%;
  width: 26%;
}

.ghost__hat--5 {
  top: 1%;
  left: 17%;
  width: 60%;
}

.ghost__hat--6 {
  top: 0;
  left: 33%;
  width: 60%;
}

.ghost__head {
  position: absolute;
  z-index: 1;
}

.ghost__head--0 {
  top: 11%;
  left: 39%;
  width: 15%;
}

.ghost__head--1 {
  top: 10%;
  left: 12%;
  width: 44%;
}

.ghost__head--2 {
  top: 9%;
  left: 36%;
  width: 27%;
}

.ghost__head--3 {
  top: 11%;
  left: 33%;
  width: 26%;
}

.ghost__head--4 {
  top: 10.5%;
  left: 36%;
  width: 19%;
}

.ghost__head--5 {
  top: 11%;
  left: 38%;
  width: 19%;
}

.ghost__head--6 {
  top: 10%;
  left: 39%;
  width: 15%;
}

.ghost__head--7 {
  top: 10%;
  left: 40%;
  width: 11%;
}

.ghost__head--8 {
  top: 11%;
  left: 38%;
  width: 17%;
}

.ghost__head--9 {
  top: 12%;
  left: 38%;
  width: 16%;
}

.ghost__head--10 {
  top: 11%;
  left: 40%;
  width: 13%;
}

.ghost__head--11 {
  top: 8%;
  left: 37%;
  width: 20%;
}

.ghost__head--12 {
  top: 8%;
  left: 39%;
  width: 16%;
}

.ghost__head--13 {
  top: 8%;
  left: 39%;
  width: 18%;
}

.ghost__head--14 {
  top: 11%;
  left: 39%;
  width: 14%;
}

.ghost__head--15 {
  top: 8%;
  left: 39%;
  width: 16%;
}

.ghost__sheet {
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.ghost__shoes {
  position: absolute;
  z-index: 1;
}

.ghost__shoes--1 {
  bottom: 2%;
  left: 58%;
  width: 24%;
}

.ghost__shoes--2 {
  bottom: 2%;
  left: 58%;
  width: 29%;
}

.ghost__shoes--3 {
  bottom: 2%;
  left: 58%;
  width: 24%;
}

.ghost img {
  width: 100%;
  height: 100%;
}

@keyframes hover {
  from {
    top: 2vh;
  }
  to {
    top: -2vh;
  }
}

.control__left, .control__right {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.control__left button, .control__right button {
  position: relative;
  width: 40px;
  height: 40px;
  background: transparent;
  background-color: #ffffff;
  border-radius: 100%;
  transition: background-color 0.3s ease 0.3s;
}

.control__left button:before, .control__right button:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  transform: translate(-50%, -50%);
  background-color: #838383;
  border-radius: 100%;
  transition: width 0.3s ease, height 0.3s ease;
  z-index: -1;
}

.control__left button:hover:before, .control__right button:hover:before {
  width: calc(100% + 8px);
  height: calc(100% + 8px);
}

.control__left button.jsloading, .control__right button.jsloading {
  background-color: #ff0000;
}

.control__left button span, .control__right button span {
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  white-space: nowrap;
  /* added line */
}

.app.js-firstvisit .control__left {
  left: -30px;
}

.app.js-firstvisit .control__right {
  right: -30px;
}

.splashscreen {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, transform 0.3s ease, visibility 0s 0.3s;
}

.splashscreen.js-grow {
  opacity: 1;
  transform: scale(1.2);
  visibility: visible;
  transition: opacity 0.3s ease, transform 0.3s ease, visibility 0s 0s;
}

.app {
  position: relative;
}

.app__inner {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100vh;
}

.app__top {
  display: flex;
  width: calc(100% - 60px);
  height: calc(100% + 200px);
  max-height: 80vh;
  justify-content: space-between;
}

@media screen and (min-width: 699px) {
  .app__top {
    width: calc(100% - 100px);
    justify-content: center;
  }
}

.app__bottom {
  display: flex;
  justify-content: center;
}

.app.js-firstvisit .app__top, .app.js-firstvisit .app__about-button {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.app.js-firstvisit .app__bottom {
  position: relative;
  top: 30px;
  opacity: 0;
  transition: all 0.3s ease;
}

.app.js-firstvisit .control__left, .app.js-firstvisit .control__right {
  opacity: 0;
  transition: all 0.3s ease, 0.3s;
}

.app.js-show .app__top, .app.js-show .app__bottom, .app.js-show .app__about-button {
  opacity: 1;
}

.app.js-show .app__bottom {
  top: 0;
}

.app.js-show .control__left {
  opacity: 1;
  left: 0;
}

.app.js-show .control__right {
  opacity: 1;
  right: 0;
}

.ghost-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(100vw - 60px);
  height: 100%;
  margin: 0 30px;
}

@media screen and (min-width: 699px) {
  .ghost-container {
    width: 30vw;
    margin: 0 50px;
  }
}

.about {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.3s ease, display 0s 0.3s;
}

.about[aria-hidden='false'] {
  display: block;
  opacity: 1;
  transition: opacity 0.3s ease, display 0s 0s;
}
